.app-header {
  display: flex;
  justify-content: space-between;
  background-color: #333;
  color: #fff;
  padding: 20px;
}

.app-header nav {
  display: flex;
  align-items: center;
}

.app-header nav a {
  color: #fff;
  margin-inline: 20px;
  font-size: larger;
}

.app-header .logo-img{
  width: 200px;
  height: 100px;
}


